<template>

    <div id="login" style="margin: 0px 400px">
        <div class="components-input-demo-presuffix" >
            <a-input placeholder="user name" v-model="userName">
                <a-icon slot="prefix" type="user"/>
                <a-tooltip slot="suffix" title="Extra information">
                    <a-icon type="info-circle" style="color: rgba(0,0,0,.45)"/>
                </a-tooltip>
            </a-input>
        </div>
        <div>
            <a-input-password placeholder="password" v-model="password">
                <a-icon slot="prefix" type="user"/>
            </a-input-password>
        </div>
        <div style="text-align: center">
            <a-button @click="login()">
                <span>登录</span>
            </a-button>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    import global_ from '../api'

    export default {

        name: "Login",
        data() {
            return {
                userName: '',
                password: '',
            };
        },
        methods: {

            emitEmpty() {
                this.$refs.userNameInput.focus();
                this.userName = '';
                this.password = '';
            },
            login() {
                var self = this;
                let data = new FormData();
                data.append('user_id', this.userName);
                data.append('password', this.password);

                // eslint-disable-next-line no-console
                console.log(global_.serve_url + '/login');
                axios
                    .post(global_.serve_url + '/login', data)
                    .then(function (response) {
                        var isLogin = response.data;
                        if (isLogin === true) {
                            self.$router.push("/recommend");
                            self.$message.info('登录成功');
                            sessionStorage.setItem("user_id", self.userName);
                        } else {
                            // eslint-disable-next-line no-console
                            self.$message.error('账号或密码错误');
                        }
                    })
                    .catch(function (error) { // 请求失败处理
                        // eslint-disable-next-line no-console
                        console.log(error);
                    });

            }

        },

    }
</script>

<style scoped>
    .components-input-demo-presuffix .anticon-close-circle {
        cursor: pointer;
        color: #ccc;
        transition: color 0.3s;
        font-size: 12px;
    }

    .components-input-demo-presuffix .anticon-close-circle:hover {
        color: #999;
    }

    .components-input-demo-presuffix .anticon-close-circle:active {
        color: #666;
    }

</style>
