<template>
    <div>
        <Banner>

        </Banner>
        <div class="panel">
            <CardPanel v-bind:movie_list="res"/>
        </div>
    </div>
</template>

<script>

    import CardPanel from "../components/CardPanel";
    import MovieCard from "../components/MovieCard";
    import axios from "axios"
    import Banner from "../components/Banner";

    import global_ from '../api'

    export default {
        name: "recommend",
        data() {
            return {
                res: [],
                user_id: sessionStorage.getItem("user_id"),
            }
        },

        // eslint-disable-next-line vue/no-unused-components
        components: {CardPanel, MovieCard, Banner},
        methods: {
            test: function () {
                var movie_id_list;
                const self = this;
                axios
                    .get(global_.serve_url + '/recommend?user_id=' + this.user_id)
                    .then(function (response) {
                        movie_id_list = response.data;
                        // eslint-disable-next-line no-console
                        console.log(movie_id_list); //输出正常数据
                        for (var movie_id of movie_id_list) {
                            // eslint-disable-next-line no-console
                            console.log(movie_id);
                            axios
                                .get(global_.serve_url + '/query_by_movie_id?movie_id=' + movie_id)
                                .then(function (response) {
                                    var movie_info = response.data;
                                    self.res.push(movie_info);
                                })
                        }
                    })
                    .catch(function (error) {
                        // eslint-disable-next-line no-console
                        console.log(error);
                    });
                return movie_id_list;
            },
        },
        mounted() {
            this.test();
            // eslint-disable-next-line no-console
            console.log(this.res);
        },
    }
</script>

