<template>
    <div>
        <CardPanel v-bind:movie_list="res"/>

    </div>
</template>

<script>

    import CardPanel from "../components/CardPanel";
    import MovieCard from "../components/MovieCard";
    import axios from "axios"
    import global_ from '../api'

    export default {
        name: "rank",
        data() {
            return {
                res: [],
                user_id: "123",
            }
        },

        // eslint-disable-next-line vue/no-unused-components
        components: {CardPanel, MovieCard},
        methods: {
            test: function () {
                const self = this;
                var movie_id_list;
                axios
                    .get(global_.serve_url + '/top_movie')
                    .then(function (response) {
                        movie_id_list = response.data;
                        // eslint-disable-next-line no-console
                        for (var movie_id of movie_id_list) {
                            // eslint-disable-next-line no-console
                            console.log(movie_id);
                            axios
                                .get(global_.serve_url + '/query_by_movie_id?movie_id=' + movie_id)
                                .then(function (response) {
                                    var movie_info = response.data;
                                    self.res.push(movie_info);
                                })
                        }

                    })
                    .catch(function (error) { // 请求失败处理
                        // eslint-disable-next-line no-console
                        console.log(error);
                    });
            }
        },
        mounted() {
            this.test();
            // eslint-disable-next-line no-console
            console.log(this.res);
        },

    }
</script>

<style scoped>
    .rank {
        margin: auto;
        text-align: center;
    }
</style>
