import Vue from 'vue';
import Antd from "ant-design-vue"
import 'ant-design-vue/dist/antd.css';
import App from './App';
import routes from "./router";
import VueRouter from "vue-router";

Vue.use(Antd);
Vue.use(VueRouter);
Vue.config.productionTip = false;
const router = new VueRouter({
    routes,
});

new Vue({
    router,
    render: h => h(App),
}).$mount('#app');
